import axios from 'axios'
import qs from 'qs'
import { Message, MessageBox } from 'element-ui'

const service = axios.create({
    baseURL: 'https://4135d8f9-23d5-4e48-8f67-d2accfad4d59.bspapp.com/http/',  // apibase_url process.env.BASE_URL
    timeout: 5000  // 
});

/****** request==> ******/
service.interceptors.request.use(config => {
    // app.$vux.loading.show({
    //     text: ''
    // });
	console.log('')
    // config.method === 'post' ? config.data = qs.stringify({...config.data}) : config.params = {...config.params};
	config.method === 'post' ? config.data = {...config.data} : config.params = {...config.data};
	console.log(config.data)
	console.log(config.params)
    // config.headers['Content-Type'] = 'application/x-www-form-urlencoded';
 
    return config;
}, error => {  //
    // app.$vux.toast.show({
    //     type: 'warn',
    //     text: error
    // });
    Promise.reject(error)
});

/****** respone==> ******/
service.interceptors.response.use(
    response => {  //
        // app.$vux.loading.hide();
        //
		console.log(response)
        if (response.data.status == 1) {
			console.log('')
            return response.data;
        } else {
            Message({
              message: response.data.msg,
              type: 'error',
              duration: 3000
            })
        }
    },
    error => {  //
        console.log('error');
        console.log(error);
        console.log(JSON.stringify(error));
 
        let text = JSON.parse(JSON.stringify(error)).response.status === 404
            ? '404'
            : '';
        // app.$vux.toast.show({
        //     type: 'warn',
        //     text: text
        // });
 
        return Promise.reject(error)
    }
);

export default service;


